.modal {
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s;
    position: absolute;
    top: 50%;
    width: 250px;
    font-size: 14px;
    padding: 5px;
    text-align: center;
    background: rgba(149, 239, 97, 0.58);
    border: 2px solid white;
    box-shadow: 12px 17px 51px rgba(0, 0, 0, 0.22);
    backdrop-filter: blur(6px);
    border-radius: 10px;
    h2, p {
        margin: 0;
    }
    &:hover {
        border: 2px solid black;
        transform: scale(1.05);
    }
    &-enter {
        opacity: 0;
        &-active {
            opacity: 1;
            visibility: visible;
            transition: all 400ms;
          }
    }
    &-exit {
        opacity: 1; 
        &-active {
            opacity: 0;
            visibility: hidden;
            transition: all 400ms;
          }
    }
}